.rab-article h1 {
  font-size: 26px;
  font-weight: normal;
}
.rab-article h2 {
  font-size: 20px;
  font-weight: normal;
}
.rab-article h3 {
  font-size: 16px;
  font-weight: normal;
}
.rab-article h4 {
  font-size: 14px;
  font-weight: normal;
}
.rab-article h5 {
  font-size: 12px;
  font-weight: normal;
}
.rab-article h6 {
  font-size: 12px;
  font-weight: normal;
}
.rab-article blockquote {
  padding: 5px 5px 3px 10px;
  line-height: 1.5;
  border-left: 4px solid #ddd;
  margin-bottom: 20px;
  color: #666;
  font-size: 14px;
}
.rab-article ul:not([class^='rab-']) {
  padding-left: 40px;
  list-style-type: disc;
}
.rab-article li:not([class^='rab-']) {
  margin-bottom: 5px;
  font-size: 14px;
}
.rab-article ul ul:not([class^='rab-']),
.rab-article ol ul:not([class^='rab-']) {
  list-style-type: circle;
}
.rab-article p {
  margin: 5px;
  font-size: 14px;
}
.rab-fade-in {
  -webkit-animation: rabFadeIn 0.3s ease-in-out;
  animation: rabFadeIn 0.3s ease-in-out;
}
.rab-fade-out {
  -webkit-animation: rabFadeOut 0.3s ease-in-out;
  animation: rabFadeOut 0.3s ease-in-out;
}
@-webkit-keyframes rabFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes rabFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes rabFadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes rabFadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes rabMessageMoveIn {
  0% {
    opacity: 0;
    padding: 0;
    transform: translateY(-40%);
  }
  100% {
    opacity: 1;
    padding: 8px;
  }
}
@keyframes rabMessageMoveIn {
  0% {
    opacity: 0;
    padding: 0;
    transform: translateY(-40%);
  }
  100% {
    opacity: 1;
    padding: 8px;
  }
}
@-webkit-keyframes rabMessageMoveOut {
  0% {
    max-height: 150px;
    padding: 8px;
    opacity: 1;
  }
  100% {
    max-height: 0;
    padding: 0;
    opacity: 0;
  }
}
@keyframes rabMessageMoveOut {
  0% {
    max-height: 150px;
    padding: 8px;
    opacity: 1;
  }
  100% {
    max-height: 0;
    padding: 0;
    opacity: 0;
  }
}
@-webkit-keyframes rabNoticeMoveIn {
  0% {
    opacity: 0;
    transform-origin: 0 0;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    transform-origin: 0 0;
    transform: translateX(0%);
  }
}
@keyframes rabNoticeMoveIn {
  0% {
    opacity: 0;
    transform-origin: 0 0;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    transform-origin: 0 0;
    transform: translateX(0%);
  }
}
@-webkit-keyframes rabNoticeMoveOut {
  0% {
    opacity: 1;
    transform-origin: 0 0;
    transform: translateX(0);
  }
